<template>
    <div class="container">
        <Nav></Nav>
        <div class="main">
            <div class="article">
                <div v-if="hasData">
                    <div class="article-head">
                        <div class="info">
                            <h3 class="title">{{articleInfo.title}}</h3>
                            <div class="o-info">
                                <span><a href="javascript:;">Mr.Gan</a></span>
                                <span>{{articleInfo.pv}}</span>
                                <span> {{articleInfo.updateDate | getTime}}</span>
                            </div>
                        </div>
                        <div class="date">
                            <div>{{articleInfo.date | year}}</div>
                            <div>{{articleInfo.date | month}}</div>
                            <div>{{articleInfo.date | date}}</div>
                        </div>
                    </div>
                    <div class="content">
                        <MarkdownPreview  :initialValue="articleInfo.content" />
                    </div>
                </div>
                <div class="no-data" v-else></div>
                <div class="description">
                    <p> Mr.Gan .</p>
                    <p> <a href="javascript:;">{{articleInfo.title}}</a></p>
                    <p> <router-link :to="'/articleDetail/' + articleInfo._id">http://http://47.104.155.124/:3000/#/articleDetail/{{articleInfo._id}}</router-link></p>
                </div>
                <div class="music"></div>
                <div class="extend" v-if="hasExtendData">
                    <div class="extend-head"></div>
                    <ul class="extend-list">
                        <li v-for="(item,index) in articleExtendList" :key="index">
                            <span></span><router-link :to="'/articleDetail/' + item._id">{{item.title}}</router-link>
                        </li>
                    </ul>
                </div>
            </div>
            <div class="operation">
                <ul>
                    <li>
                        <span></span>
                    </li>
                    <li><span></span></li>
                    <li><span></span></li>
                </ul>
            </div>
            <div class="comment">
                <h3></h3>
                <RichText @Sub="submitComment"></RichText>
            </div>
            <ul class="comment-list">
                <li class="comment-item" v-for="(item,index) in commentList" :key="item._id">
                    <div class="p-avatar" :style="{'backgroundImage': 'url('+item.user.avatar+')'}"></div>
                    <div class="main-info">
                        <div class="p-username">{{item.user.username}}</div>
                        <div class="p-content" v-html="item.content">
                        </div>
                        <div class="time">
                            <span>{{item.date}}</span>
                            <span @click="replyClick(index)">
                                {{(item.reply.lastIndexArr[0]===index && item.reply.lastIndexArr[1]===undefined)?"":""}}
                            </span>
                        </div>
                        <ul class="sub-list">
                            <li class="sub-item" v-for="(subItem,childIndex) in item.children">
                                <div class="sub-avatar" :style="{'backgroundImage': 'url('+subItem.user.avatar+')'}"></div>
                                <div>
                                    <div class="sub-info">
                                        <span>{{subItem.user.username}}</span>
                                        <span></span>
                                        <span>{{subItem.atUserName}}</span>
                                    </div>
                                    <div class="sub-content">
                                        {{subItem.content}}
                                    </div>
                                    <div class="time">
                                        <span>{{subItem.date}}</span>
                                        <span @click="replyClick(index,childIndex)">
                                            {{(item.reply.lastIndexArr[0]===index && item.reply.lastIndexArr[1]===childIndex)?"":""}}
                                        </span>
                                    </div>
                                </div>
                            </li>
                        </ul>
                        <div :class="['reply',{'show': item.reply.ifShow}]">
                            <textarea :placeholder="''+ item.reply.atUserName+''" v-model="item.reply.content"></textarea>
                            <el-button type="success" size="mini" @click="submitSubComment(index)"></el-button>
                        </div>
                    </div>
                </li>
            </ul>
        </div>
    </div>
</template>

<script>
    import Nav from "../../components/Nav";
    import {MarkdownPreview } from 'vue-meditor';
    import RichText from "../../components/RichText";
    import articleService from "../../api/articleService";
    import userService from "../../api/userService";
    export default {
        name: "ArticleDetail",
        components: {
            Nav,RichText,MarkdownPreview
        },
        data(){
            return{
                articleInfo: {},
                hasData: false,
                id: '',
                articleExtendList: [],
                hasExtendData: false,
                commentList: []
            }
        },
        filters: {
            getTime(value) {
                let date = new Date(value);
                return date.toDateString() + ' ' + date.toLocaleTimeString();
            },
            date(value) {
                let date = new Date(value);
                return date.getDate();
            },
            month(value) {
                let date = new Date(value);
                return date.getMonth() + 1;
            },
            year(value) {
                let date = new Date(value);
                return date.getFullYear();
            }
        },
        mounted() {
            this.id = this.$route.params.id;
            this.getArticleDetailById();
            this.getArticleCommentByArticleId();
        },
        methods: {
            getArticleDetailById() {
                articleService.getArticleDetailById(this.id).then(res => {
                   let data = res.data;
                   if (data.code === 0){
                       this.articleInfo = data.data;
                       this.getArticleListExtend(this.articleInfo.tag);
                       this.hasData = true
                   }else{
                       this.$router.push('/notfound');
                   }
                }).catch(err => {
                    this.$router.push('/notfound');
                });
            },
            getArticleListExtend(tag) {
                articleService.getArticleListExtend(tag).then(res => {
                    let data = res.data;
                    if (data.code === 0){
                        this.articleExtendList = data.data;
                        this.hasExtendData = true;
                    }else if (data.code === 1){
                        this.articleExtendList = data.data;
                        this.hasExtendData = false
                    }
                })
            },
            getArticleCommentByArticleId() {
                articleService.getArticleCommentByArticleId(this.id).then(res => {
                    let data = res.data.comment;
                    data.map(item => {
                        item.reply = {
                            userId : "",//id
                            content : "",//
                            atUserName : "",//
                            date : "",//
                            ifShow : false,
                            lastIndexArr : []
                        };
                        return item;
                    });
                    this.commentList = data;
                });
            },
            submitComment(value) {
                if (!value){
                    layer.msg('', {icon: 2});
                    return false;
                }
                userService.getIfUserLogin().then(res => {
                    let data = res.data;
                    if (!data.ifLogin) {
                        layer.msg('', {icon: 2});
                    }else{
                        articleService.addArticleCommentById(this.id,data.data._id,value).then(res => {
                            if (res.data.code === 0){
                                layer.msg('', {icon: 1});
                                //
                                // TODO 
                                setTimeout(function () {
                                    window.location.reload()
                                },1000)
                            }else{
                                layer.msg(res.data.msg, {icon: 2});
                            }
                        }).catch(err => {
                            layer.msg('~', {icon: 2});
                        })
                    }
                });
            },
            replyClick(pIndex,cIndex){
                if (pIndex === undefined)return;
                let parentData = this.commentList[pIndex];
                //
                if ((pIndex === parentData.reply.lastIndexArr[0] && cIndex === parentData.reply.lastIndexArr[1])) {
                    parentData.reply.ifShow = false;
                    parentData.reply.lastIndexArr=[];
                }else{
                    //
                    parentData.reply.ifShow = true;
                    parentData.reply.lastIndexArr=[pIndex,cIndex];
                }
                if (cIndex === undefined){
                    //
                    parentData.reply.atUserName = parentData.user.username;
                }else{
                    //
                    parentData.reply.atUserName = parentData.children[cIndex].user.username
                }
            },
            submitSubComment(index) {
                //
                userService.getIfUserLogin().then(res => {
                    let data = res.data;
                    if (!data.ifLogin) {
                        layer.msg('', {icon: 2});
                    }else{
                        articleService.addArticleSubCommentById({
                            commentId: this.commentList[index]._id,
                            userId: data.data._id,
                            content: this.commentList[index].reply.content,
                            atUserName: this.commentList[index].reply.atUserName}
                        ).then(res => {
                            if (res.data.code === 0){
                                layer.msg('', {icon: 1});
                                //
                                // TODO 
                                setTimeout(function () {
                                    window.location.reload()
                                },1000)
                            }else{
                                layer.msg(res.data.msg, {icon: 2});
                            }
                        }).catch(err => {
                            layer.msg('~', {icon: 2});
                        })
                    }
                })
            }
        },
        watch: {
            $route (to, from) {
                this.$router.go(0)
            }
        }
    }
</script>

<style lang="less" scoped>
.container{
    width: 100%;
    box-sizing: border-box;
    padding: 70px 50px;
    .main{
        max-width: 1360px;
        width: 100%;
        margin: 0 auto;
        background-color: #fff;
        padding: 30px 25px 30px;
        > .article{
            width: 100%;
            .article-head{
                width: 100%;
                height: 60px;
                box-sizing: border-box;
                border-bottom: 1px solid #eee;
                margin-bottom: 10px;
                div.info{
                    float: left;
                    h3.title{
                        font-size: 20px;
                        margin-bottom: 7px;
                    }
                    div.o-info{
                        color: #787977;
                        span{
                            margin: 0 7px;
                        }
                        >span:nth-child(1){
                            a{
                                color: #3e8bc7;
                            }
                            a:hover{
                                text-decoration: underline;
                            }
                        }
                    }
                }
                div.date{
                    height: 60px;
                    float: right;
                    position: relative;
                    div{
                        position: absolute;
                        font-size: 15px;
                    }
                    div:nth-child(1){
                        color: #787977;
                        bottom: 16px;
                        left: -30px;
                    }
                    div:nth-child(2){
                        color: #787977;
                        bottom: 15px;
                        left: -60px;
                    }
                    div:nth-child(3){
                        font-size: 40px;
                        color: #f07c82;
                        top: -16px;
                        left: -45px;
                    }
                }
            }
            .content{
                margin: 10px 0 20px;
                line-height: 28px;
            }
            .no-data{
                width: 100%;
                height: 200px;
                line-height: 200px;
                text-align: center;
            }
            .description{
                background-color: #f8f9f7;
                padding: 15px 20px;
                line-height: 22px;
                text-shadow: 0 1px 0 rgba(255,255,255,.8);
                font-size: 12px;
                color: #787977;
                p:nth-child(2),p:nth-child(3){
                    a{
                        color: #3e8bc7;
                        font-size: 15px;
                    }
                }
            }
            .extend{
                width: 100%;
                margin: 20px 0 0;
                .extend-head{
                    background-color: #f8f9f7;
                    font-size: 18px;
                    padding: 7px 0 7px 20px;
                    border-left: 3px solid #f07c82;
                }
                .extend-list{
                    padding: 20px 0;
                    li{
                        line-height: 28px;
                        span{
                            margin-right: 8px;
                        }
                        a{
                            color: #3e8bc7
                        }
                    }
                }
            }
        }
        > .operation{
            box-sizing: border-box;
            width: 100%;
            padding: 25px;
            border-top: 1px solid #eee;
            border-bottom: 1px solid #eee;
            ul{
                width: 300px;
                margin: 0 auto;
                display: flex;
                justify-content: space-between;
                li{
                    width: 60px;
                    height: 60px;
                    box-sizing: border-box;
                    text-align: center;
                    line-height: 85px;
                    cursor: pointer;
                    border-radius: 50%;
                    span{
                        display: inline-block;
                        width: 40px;
                        height: 40px;
                    }
                    &:hover span{
                        opacity: .6;
                    }
                }
                li:nth-child(1){
                    border: 2px solid #5AC64F;
                }
                li:nth-child(1) span{
                    background: url('../../assets/img/shareicon.png') no-repeat 0 0/ cover;
                }
                li:nth-child(2){
                    border: 2px solid #FF7171;
                }
                li:nth-child(2) span{
                    background: url('../../assets/img/shareicon.png') no-repeat center / cover;
                }
                li:nth-child(3){
                    border: 2px solid #4CAFE9;
                }
                li:nth-child(3) span{
                    background: url('../../assets/img/qq.png') no-repeat center / cover;
                }
            }
        }
        > .comment{
            position: relative;
            width: 100%;
            box-sizing: border-box;
            border-top: 1px solid #eee;
            margin-top: 20px;
            h3{
                position: absolute;
                background-color: #fff;
                height: 30px;
                line-height: 30px;
                width: 100px;
                text-align:  center;
                top: -38px;
                left: 20px;
                font-size: 20px;
                font-weight: 300;
            }
        }
        > .comment-list{
            width: 100%;
            box-sizing: border-box;
            padding: 20px 0 0;
            > li.comment-item{
                width: 100%;
                box-sizing: border-box;
                display: flex;
                border-bottom: 1px dashed #eee;
                margin-bottom: 7px;
                padding-bottom: 10px;
                > div.p-avatar{
                    box-sizing: border-box;
                    width: 50px;
                    height: 50px;
                    background-size: cover;
                    border-radius: 50%;
                }
                > div.main-info{
                    width: 100%;
                    box-sizing: border-box;
                    margin-left: 10px;
                    overflow: hidden;
                    > .p-username{
                        height: 50px;
                        line-height: 50px;
                    }
                    > .p-content{
                        margin: 7px 0;
                    }
                    > .time{
                        color: #7a7374;
                        margin-bottom: 7px;
                        padding-bottom: 7px;
                        span:nth-child(1){
                            margin-right: 7px;
                        }
                        span:nth-child(2){
                            color: #2177b8;
                            cursor: pointer;
                            &:hover{
                                text-decoration: underline;
                            }
                        }
                    }
                    > ul.sub-list{
                        width: 100%;
                        box-sizing: border-box;
                        li.sub-item{
                            position: relative;
                            width: 100%;
                            display: flex;
                            .sub-avatar{
                                width: 50px;
                                height: 50px;
                                box-sizing: border-box;
                                background-size: cover;
                                border-radius: 50%;
                                margin-right: 10px;
                            }
                            .sub-info{
                                top: 0;
                                left: 60px;
                                span{
                                    margin-right: 10px;
                                }
                                span:nth-child(1),span:nth-child(3){
                                    color: #2177b8;
                                }
                            }
                            .sub-content{
                                margin: 10px 0;
                            }
                            .time{
                                color: #7a7374;
                                margin-bottom: 7px;
                                padding-bottom: 7px;
                                span:nth-child(1){
                                    margin-right: 7px;
                                }
                                span:nth-child(2){
                                    color: #2177b8;
                                    cursor: pointer;
                                    &:hover{
                                        text-decoration: underline;
                                    }
                                }
                            }
                        }
                    }
                    >div.reply{
                        width: 100%;
                        height: 0;
                        transition: height .3s;
                        &.show{
                            height: 110px;
                        }
                        textarea{
                            box-sizing: border-box;
                            padding: 10px;
                            width: 100%;
                            height: 60px;
                            display: block;
                            margin-bottom: 10px;
                        }
                    }
                }
            }
        }
    }
}
</style>